@Composable
internal fun DynamicUI() {
    val coroutineScope = rememberCoroutineScope()
    var component by remember { mutableStateOf<ComponentUIModel?>(null) }
    var textt by remember { mutableStateOf("init") }

    LaunchedEffect(Unit) {
        coroutineScope.launch {
            while (true) {
                try {
                    val result: DCComponent =
                        fetchApiResponse("https://private-c69899-dynamiccomponent.apiary-mock.com/dc")
                    textt = jsonKt.encodeToString(result)
                    component = result.toUIModel()
                } catch (t: Throwable) {
                    textt = t.stackTraceToString()
                }
                delay(1000)
            }
        }
    }

    Box(
        modifier = Modifier.fillMaxSize()
    ) {
//        component?.let {
//            DynamicComponent(it, this)
//        } ?: run {
            Text(text = textt)
//        }
    }

}

suspend fun fetchApiResponse(url: String): DCComponent {
    val client = HttpClient {
        install(ContentNegotiation) {
            json(jsonKt)
        }
        install(Logging) {
            logger = Logger.DEFAULT
            level = LogLevel.INFO
        }
    }
    return client.use { it.get(url).body() }
}